import React, { createRef, useState } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import { ReactComponent as PwdIcon } from "../../assets/icons/password.svg";
import styles from "./ResetPwd.module.css";
import UserApi from "../../api/UserApi.js";

const ResetPwd = () => {
  const { getPwdResetResult } = UserApi;
  // props   
  const pwdRef = createRef();
  const confirmPwdRef = createRef();
  const [pwd, setPwd] = useState("");
  const [confirmPwd, setConfirmPwd] = useState("");
  const [pwdCheck, setPwdCheck] = useState(false);
  const [confirmPwdCheck, setConfirmPwdCheck] = useState(false);
  const [pwdMsg, setPwdMsg] = useState("");
  const [confirmPwdMsg, setConfirmPwdMsg] = useState("");
  const [errorMsg, setErrorMsg] = useState("");

  //URL params 
  const [searchParams] = useSearchParams();
  const id = searchParams.get("userId");
  const authCode = searchParams.get("authCode");

  const navigate = useNavigate();

  //   
  const handleInput = (event) => {
    const pwdPattern =
      /^(?=.*[a-zA-Z])(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{8,20}$/;
    const { id: target, value } = event.target;
    switch (target) {
      case "pwd":
        setPwd(value);
        if (!pwdPattern.test(value)) {
          setPwdMsg("8 ~ 20 , ,  ");
        } else {
          setPwdMsg("");
          setPwdCheck(true);
        }
        break;
      case "confirmPwd":
        setConfirmPwd(value);
        if (pwd !== value) {
          setConfirmPwdMsg("  .");
        } else {
          setConfirmPwdMsg("");
          setConfirmPwdCheck(true);
        }
        break;

      default:
        console.log("error");
    }
  };

  const pwdRestApi = async () => {
    try {
      const { data } = await getPwdResetResult({
        userId: id,
        password: pwd,
        authCode: authCode,
      });
      console.log(data);
      setPwdMsg("");
      navigate("/user/login");
    } catch ({ response }) {
      setErrorMsg(" .");
    }
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    if (!pwdCheck) {
      setPwdMsg(" .");
      pwdRef.current.focus();
    } else if (!confirmPwdCheck) {
      setConfirmPwdMsg("   ");
      confirmPwdRef.current.focus();
    } else {
      const data = {
        pwd,
      };
      // api 
      console.log(data);
      //     

      pwdRestApi();
    }
  };

  return (
    <div className={styles.formBorder}>
      <form className={styles.form} onSubmit={handleSubmit}>
        <div className={styles.title}> </div>
        <div className={styles.findIdForm}>
          <div className={styles.inputArea}>
            {/*  */}
            <div className={styles.inputRow}>
              <div className="">
                <PwdIcon className={styles.icon} />
              </div>
              <div className={styles.input}>
                <input
                  ref={pwdRef}
                  autoComplete="off"
                  id="pwd"
                  className={styles.inputData}
                  value={pwd}
                  type="password"
                  placeholder=""
                  onChange={handleInput}
                />
                <p className={styles.invalidMsg}>{pwdMsg}</p>
              </div>
            </div>
            {/*   */}
            <div className={styles.inputRow}>
              <div>
                <PwdIcon className={styles.icon} />
              </div>
              <div className={styles.input}>
                <input
                  autoComplete="off"
                  id="confirmPwd"
                  className={styles.inputData}
                  value={confirmPwd}
                  type="password"
                  placeholder=" "
                  ref={confirmPwdRef}
                  onChange={handleInput}
                />
                <p className={styles.invalidMsg}>{confirmPwdMsg}</p>
              </div>
            </div>
          </div>
          <p className={styles.invalidMsg}>{errorMsg}</p>
          <button className={styles.nextBtn} type="submit">
            
          </button>
        </div>
      </form>
    </div>
  );
};

export default ResetPwd;
