import React from "react";
import { Link } from "react-router-dom";
import { ReactComponent as NameIcon } from "../../assets/icons/name.svg";
import styles from "./FindId.module.css";

const ShowId = ({ id, name }) => {
  const maskingId = (str) => {
    let originStr = str;
    let maskingStr;
    let strLength;

    strLength = originStr.length;
    if (strLength < 3) {
      maskingStr = originStr.replace(/(?<=.{1})./gi, "*");
    } else {
      maskingStr = originStr.replace(/(?<=.{2})./gi, "*");
    }
    return maskingStr;
  };

  const securityId = maskingId(id);

  return (
    <>
      <div className={styles.resultTitle}>
        <div className={styles.content}>
          {name}    .
        </div>
        <span className={styles.info}>
               *  .
        </span>
      </div>
      <div className={styles.resultId}>
        <NameIcon fill="#EEE" width="20" height="20" />
        <span className={styles.idInfo}>: {securityId}</span>
        {/* <span className={styles.idInfo}>: {id}</span> */}
      </div>

      <div className={styles.confirmButtons}>
        <Link to="/user/login">
          <button className={styles.loginBtn}></button>
        </Link>

        <Link to="/user/findPwd">
          <button className={styles.findPwdBtn}> </button>
        </Link>
      </div>
    </>
  );
};

export default ShowId;
