import React, { createRef, useState } from "react";
import styles from "./FindId.module.css";
import { ReactComponent as EmailIcon } from "../../assets/icons/email.svg";
import { ReactComponent as NameIcon } from "../../assets/icons/name.svg";
import { ReactComponent as EmailConfirmIcon } from "../../assets/icons/confirm.svg";
import ShowId from "./ShowId";
import UserApi from "../../api/UserApi.js";

const FindId = () => {
  const { getEmailCheckResult, getEmailCodeCheckResult } = UserApi;

  const emailRef = createRef();
  const confirmEmailRef = createRef();
  const nameRef = createRef();

  const [confirm, setConfirm] = useState(false);

  //   
  const [id, setId] = useState("");
  const [email, setEmail] = useState("");
  const [emailConfirmCode, setEmailConfirmCode] = useState("");
  const [name, setName] = useState("");

  //   
  const [emailMsg, setEmailMsg] = useState("");
  const [emailConfirmCodeMsg, setEmailConfirmCodeMsg] = useState("");
  const [nameMsg, setNameMsg] = useState("");

  //    flag
  const [emailCheck, setEmailCheck] = useState(false);
  // const [emailConfirmCheck, setEmailConfirmCheck] = useState(false);
  const [nameCheck, setNameCheck] = useState(false);
  //     
  const [isSend, setIsSend] = useState(false);

  // ,   
  const [emailConfirm, setEmailConfirm] = useState(false);

  //   
  const handleInput = (event) => {
    const emailPattern =
      /^([0-9a-zA-Z_.-]+)@([0-9a-zA-Z_-]+)(\.[0-9a-zA-Z_-]+){1,2}$/;
    const namePattern = /^[-]+$/;

    const { id: target, value } = event.target;
    switch (target) {
      case "email":
        setEmail(value);
        if (!emailPattern.test(value)) {
          setEmailMsg("   .");
        } else {
          setEmailMsg("");
          setEmailCheck(true);
        }
        break;
      case "emailConfirmCode":
        setEmailConfirmCode(value);
        break;
      case "name":
        setName(value);
        if (!namePattern.test(value)) {
          setNameMsg("  .");
        } else {
          setNameMsg("");
          setNameCheck(true);
        }
        break;
      default:
        console.log("error");
    }
  };

  //   
  const handleEmailCheck = async () => {
    //    api 
    try {
      const { data } = await getEmailCheckResult("findId", {
        name: name,
        email: email,
      });
      setIsSend(true);
      setEmailMsg(" .  .");
    } catch ({ response }) {
      console.log(response);
      setEmailMsg("   .");
    }
  };

  const handleEmailCodeCheck = async () => {
    try {
      const { data } = await getEmailCodeCheckResult("findId", {
        name: name,
        email: email,
        authCode: emailConfirmCode,
      });
      setEmailConfirm(true);
      setId(data.userId);
      setEmailConfirmCodeMsg(" .");
    } catch ({ response }) {
      setEmailConfirmCodeMsg(" .");
    }
  };

  //     
  const handleSubmit = (event) => {
    event.preventDefault();
    if (!nameCheck) {
      setNameMsg(" .");
      nameRef.current.focus();
    } else if (!emailCheck) {
      setEmailMsg(" .");
      emailRef.current.focus();
    } else if (!isSend) {
      setEmailMsg("  .");
    } else if (!emailConfirm) {
      setEmailConfirmCodeMsg(" .");
      confirmEmailRef.current.focus();
    } else {
      setConfirm(true);
    }
  };

  return (
    <div className={styles.formBorder}>
      <form className={styles.form} onSubmit={handleSubmit}>
        <div className={styles.title}> </div>
        {confirm ? (
          <ShowId id={id} name={name} />
        ) : (
          <div className={styles.findIdForm}>
            <div className={styles.inputArea}>
              <div className={styles.inputRow}>
                <div>
                  <NameIcon fill="#EEE" width="30" height="30" />
                </div>
                <div className={styles.input}>
                  <input
                    autoComplete="off"
                    id="name"
                    className={styles.inputData}
                    value={name}
                    type="text"
                    placeholder=""
                    ref={nameRef}
                    onChange={handleInput}
                  />
                  <p className={styles.invalidMsg}>{nameMsg}</p>
                </div>
              </div>

              {/*  */}
              <div className={styles.inputRow}>
                <div>
                  <EmailIcon fill="#EEE" width="30" height="30" />
                </div>
                <div className={styles.input}>
                  <div className={styles.inputWithBtn}>
                    <input
                      autoComplete="off"
                      id="email"
                      className={styles.inputData}
                      value={email}
                      type="email"
                      placeholder=""
                      disabled={emailConfirm ? true : false}
                      ref={emailRef}
                      onChange={handleInput}
                    />
                    <button
                      type="button"
                      className={styles.checkBtn}
                      onClick={handleEmailCheck}
                      disabled={emailCheck ? false : true}
                    >
                      
                    </button>
                  </div>
                  {isSend ? (
                    <p className={styles.validMsg}>{emailMsg}</p>
                  ) : (
                    <p className={styles.invalidMsg}>{emailMsg}</p>
                  )}
                </div>
              </div>
              {/*   */}
              {isSend && (
                <div className={styles.inputRow}>
                  <div>
                    <EmailConfirmIcon fill="#EEE" width="30" height="30" />
                  </div>
                  <div className={styles.input}>
                    <div className={styles.inputWithBtn}>
                      <input
                        autoComplete="off"
                        id="emailConfirmCode"
                        className={styles.inputData}
                        value={emailConfirmCode}
                        type="text"
                        placeholder=" "
                        ref={confirmEmailRef}
                        onChange={handleInput}
                      />
                      <button
                        type="button"
                        className={styles.confirmBtn}
                        onClick={handleEmailCodeCheck}
                        disabled={emailCheck ? false : true}
                      >
                        
                      </button>
                    </div>
                    {emailConfirm ? (
                      <p className={styles.validMsg}>{emailConfirmCodeMsg}</p>
                    ) : (
                      <p className={styles.invalidMsg}>{emailConfirmCodeMsg}</p>
                    )}
                  </div>
                </div>
              )}
            </div>

            <button className={styles.nextBtn} type="submit">
              
            </button>
          </div>
        )}
      </form>
    </div>
  );
};

export default FindId;
