import React, { useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import styles from "./Login.module.css";
import { Link } from "react-router-dom";
import { ReactComponent as IdIcon } from "../../assets/icons/userid.svg";
import { ReactComponent as PwdIcon } from "../../assets/icons/password.svg";
import logo from "../../assets/icons/logo.png";
import UserApi from "../../api/UserApi.js";
import LoginStatusContext from "../../contexts/LoginStatusContext";
import GoogleLoginBtn from "./GoogleLogin";

const Login = () => {
  const { setLoginStatus } = useContext(LoginStatusContext);

  const [input, setInput] = useState({
    id: "",
    password: "",
  });

  // import login api
  const { getLoginResult } = UserApi;

  const { id, password } = input;

  //input    
  const [idMsg, setIdMsg] = useState(" ");
  const [pwdMsg, setPwdMsg] = useState(" ");

  //login error msg
  const [loginMsg, setLoginMsg] = useState(" ");

  const [idCheck, setIdCheck] = useState(false);
  const [pwdCheck, setPwdCheck] = useState(false);

  //  
  // const [isLogin, setIslogin] = useState(false);

  //navigator
  const navigate = useNavigate();

  //input  
  const handleInput = (event) => {
    let pwdRule = /^.*(?=^.{8,20}$)(?=.*\d)(?=.*[a-zA-Z])(?=.*[!@#$%^&+=]).*$/;
    const { id, value } = event.target;
    setInput({
      ...input,
      [id]: value,
    });

    setLoginMsg("");

    if (id === "id") {
      if (value.length < 5 && value.length > 0) {
        setIdMsg("5   .");
        setIdCheck(false);
      } else if (value.length > 16) {
        setIdCheck(false);
        setIdMsg("16   .");
      } else if (value === "") {
        setIdCheck(false);
        setIdMsg(" ");
      } else {
        setIdCheck(true);
        setIdMsg("");
      }
    }
    if (id === "password") {
      if (!pwdRule.test(value) && value.length > 0) {
        console.log(1);
        setPwdCheck(false);
        setPwdMsg(
          "  8~20 , ,   "
        );
      } else if (value.length === 0) {
        setPwdCheck(false);
        console.log(2);
        setPwdMsg(" ");
      } else {
        console.log(3);
        setPwdCheck(true);
        setPwdMsg("");
      }
    }
  };

  const checkValidation = (...input) => {
    if (id.length === 0) setIdMsg(" ");
    if (password.length === 0) setPwdMsg(" ");
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    checkValidation();

    if (idCheck === true && pwdCheck === true) {
      loginApi();
    }
  };

  //  
  // const checkToken = () => {};

  const loginApi = async () => {
    let userError = 404;
    let pwdError = 401;
    let errorMsg =
      "     .\n     .";
    const body = {
      userId: input.id,
      password: input.password,
    };
    try {
      const { data } = await getLoginResult(body);
      setLoginMsg("");
      sessionStorage.setItem("accessToken", data.accessToken);
      setLoginStatus("2");
      // onLoginChange("2");
      navigate("/");
    } catch ({ response }) {
      if (
        response.data.statusCode === userError ||
        response.data.statusCode === pwdError
      ) {
        setLoginMsg(errorMsg);
      }
    }
  };

  //    
  return (
    <>
      <div className={styles.formBorder}>
        <form className={styles.form} onSubmit={handleSubmit}>
          <img src={logo} alt="logo" className={styles.logo} />
          <div className={styles.inputArea}>
            <div className={styles.inputRow}>
              <div>
                <IdIcon className={styles.icon} />
              </div>
              <div className={styles.input}>
                <input
                  className={styles.inputData}
                  id="id"
                  placeholder=""
                  type="text"
                  onChange={handleInput}
                  autoComplete="off"
                />
                {/* {idCheck === false ? <p className={styles.errorMsg}>{idMsg}</p> : null} */}
                <p className={styles.errorMsg}>{idMsg}</p>
              </div>
            </div>

            <div className={styles.inputRow}>
              <div>
                <PwdIcon className={styles.icon} />
              </div>
              <div className={styles.input}>
                <input
                  className={styles.inputData}
                  id="password"
                  placeholder=""
                  type="password"
                  onChange={handleInput}
                  autoComplete="off"
                />
                {/* {pwdCheck === false ? <p className={styles.errorMsg}>{pwdMsg}</p> : null} */}
                <p className={styles.errorMsg}>{pwdMsg}</p>
              </div>
            </div>
          </div>

          <p className={styles.errorMsg}>{loginMsg}</p>

          <div className={styles.confirmButtons}>
            <button
              className={styles.loginBtn}
              // disabled={disabled}
              type="submit"
            >
              
            </button>

            {/*  
            {isLogin ? <p>{window.localStorage.getItem("id")}</p> : <> </>} */}

            <Link to="/user/signup">
              <button className={styles.registBtn}></button>
            </Link>
          </div>

          <div className={styles.findButtons}>
            <Link to="/user/findId">
              <button className={styles.findIdBtn}> </button>
            </Link>
            <div className={styles.updown}></div>
            <Link to="/user/findPwd">
              <button className={styles.findPwdBtn}> </button>
            </Link>
          </div>
          <GoogleLoginBtn />

          {/* <KakaoBtn/>
            <GoogleBtn/> */}
        </form>
      </div>
    </>
  );
};

// function KakaoBtn() {
//     return(
//         <button> </button>
//     )
// }

// function GoogleBtn() {
//     return(
//         <button> </button>
//     )
// }
export default Login;
